import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { StorageService } from '../@core/storage.service';
import { ICookies } from '../@shared/interfaces/cookies';
import * as dayjs from 'dayjs';
import { Constants } from '../@global/constants';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { AuthState } from '../@store/auth/auth.state';
import { SetAnalyticsCookies } from '../@store/common/common.actions';
import { Location } from '@angular/common';
import { SignOut } from '../@store/auth/auth.actions';

@Component({
  selector: 'app-cookies',
  templateUrl: './cookies.page.html',
  styleUrls: ['./cookies.page.scss'],
})
export class CookiesPage implements OnInit {
  public logoPath = Constants.LOGO_PATH;
  private cookiesModel: ICookies;
  public formGroup: FormGroup;
  public clickedConfigure = false;
  constructor(
    private router: Router,
    private store: Store,
    private storage: StorageService,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private iab: InAppBrowser,
    private location: Location
  ) {}

  async ngOnInit() {
    this.initForm();
  }

  private initForm(): void {
    this.formGroup = this.formBuilder.group({
      firstCheck: [true],
      secondCheck: [true],
      thirdCheck: [true],
    });
  }

  public async onConfigureButtonClick() {
    this.clickedConfigure = true;
  }

  public openCompleteCookies() {
    this.iab.create(Constants.COMPLETE_COOKIES_URL);
  }

  public goBack() {
    this.store.dispatch(new SignOut());
    this.location.back();
  }

  public async onAcceptButtonClick() {
    this.cookiesModel = {
      creationDate: dayjs().format(Constants.DATE_DISPLAY_FORMAT).toString(),
      cookies: {
        tech: true,
        analytics: this.formGroup.get('secondCheck').value,
        socialMedia: this.formGroup.get('thirdCheck').value,
      },
    };
    await this.storage.setCookiesData(this.cookiesModel);
    await this.store.dispatch(new SetAnalyticsCookies(this.formGroup.get('secondCheck').value)).toPromise();
    if (!!this.store.selectSnapshot(AuthState.getToken)) {
      this.router.navigate([Constants.FIRST_PAGE_AFTER_LOGIN]);
    } else {
      this.router.navigate([Constants.PAGE_AFTER_COOKIES]);
    }
  }
}
